export default {
	props: {
		imgList: {
			type: Array,
			default: []
		},
		autoplay: {
			type: Boolean,
			default: true
		},
		show: {
			type: Boolean,
			default: true
		},
	},
	data(){
		return {
			currentIndex: 0
		}
	},
	methods: {
		handleChange(e) {
			// console.log(e)
			this.currentIndex = e.detail.current
			this.changeList()
		},
		goImg(type,content) {
			this.$emit('goImg',type,content)
		},
		changeList() {
			this.$emit('changeList',this.currentIndex)
		}
	}
}	
